
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins",sans-serif;
  scroll-behavior: smooth;
}

:root{
  --primary : #1f3e72;
  --secondary : rgba(255,255,255,0.78);
  --black : #131110;
  --blue-gradient:linear-gradient(97.05deg,#4066ff 3.76%,#2949c6 100%);
  --orange-gradient:linear-gradient(270deg,#ffb978 0%,#ff922d 100%);
  --blue : #4066ff;
  --lightBlue : #eeeeff;
  --shadow : 0px 23px 21px -8px rgba(136,160,255,0.25);
}

a{
  color: inherit;
  text-decoration: none;
}
.paddings{
  padding: 1.5rem;
}
.innerwidth{
  width: 100%;
}
.flexCenter{
  display: flex;
  row-gap: 2rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.flexStart{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.flexEnd{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.flexColCenter{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.flexColStart{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.flexColEnd{
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}
.primaryText{
    color: #1f3e72;
    font-weight: bold;
    font-size: 2rem;
}
.secondaryText{
  color: rgb(140,139 , 139);
  font-size: 0.9rem;
}
.orangeText{
  color: orange;
  font-size: 1.5rem;
  font-weight: auto;
}
.button{
  font-weight: 500;
  padding: 0.6rem 1.4rem;
  color: white;
  background: var(--blue-gradient);
  border: none;
  border-radius: 4px;
  transition: all 300ms ease-in;
}
.button:hover{
  cursor: pointer;
  transform: scale(1.1);
}
@media (min-width : 1536px){
  .innerwidth{
    max-width: 1280px;
    margin: auto;
  }
}
@media (min-width : 640px){
  .paddings{
    padding: 4rem;
  }
}
@media (min-width : 1280px), (min-width : 768px){
  .paddings{
    padding: 2rem;
  }
}
@media (max-width : 640px){
  .primaryText{
    font-size: 1.5rem;
  }
  .orangeText{
    font-size: 1.2rem;
  }
}
