.hero-wrapper {
    position: relative;
    overflow: hidden;
}
.navbar {
    position: fixed; 
    top: 0; 
    width: 100%; 
    background-color: #fff;
    z-index : 1000;
}
.hero-wrapper {
    padding-top: 60px; 
}
.h-wrapper{
    background: var(--black);
    color: white;
}
.h-container{
    justify-content: space-between;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: var(--secondary);
}
.h-menu{
    gap: 2rem;
}
.h-menu>*:hover{
    cursor: pointer;
}
.menu-icon{
    display: none;
}
@media(max-width:768px){
    .menu-icon{
        display: block;
    }
    .h-menu{
        color: var(--black);
        position: absolute;
        top: 3rem;
        right: 4rem;
        background: white;
        flex-direction: column;
        font-weight: 500;
        gap: 2rem;
        padding: 3rem;
        border-radius: 10px;
        align-items: flex-start;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
        transition: all 300ms ease-in;
    }
}


